import { SET_ALERT, REMOVE_ALERT } from "../actions/types";
const initialState = [];
export default function (state = initialState, { type, payload }) {
  switch (type) {
    case SET_ALERT:
      return [...state, payload];
    case REMOVE_ALERT:
      return state.filter((alert) => alert.id !== payload.id);
    default:
      return state;
  }
}
